import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {SceneRenderer} from "../scene-renderer";
import {SceneRef} from "../../../scene-ui/scene-ref";
import {Journey} from "../../../rpg-engine/scenes/journey";
import {createElementCssSelector} from "@angular/compiler";
import Konva from "konva";
import {WalkRenderer} from "../lib/walk-renderer";

@Component({
	selector: 'app-journey-step',
	templateUrl: './journey-step.component.html',
	styleUrls: ['./journey-step.component.scss']
})
export class JourneyStepComponent extends SceneRenderer implements OnInit, AfterViewInit {

	@ViewChild("renderholder", {static: false}) renderholder: ElementRef;
	@ViewChild("renderview", {static: false}) renderview: ElementRef;

	journeyscene: Journey;
	viewrenderer: WalkRenderer;

	// TILESBYTERRRAIN = {
	// 	"M": "basetilemountain400.png",
	// 	"H": "basetilehills400.png",
	// 	"F": "basetileforest400.png",
	// 	"P": "basetileplains400.png",
	// };

	TILESBYTERRRAIN = {
		"M": "basetilemountain.png",
		"H": "basetilehills.png",
		"F": "basetileforest.png",
		"P": "basetileplains.png",
	};
	PATH_TILES = "assets/graph/fptiles/";
	terraintiles: Map<string, string>;
	tileimages: Map<string, HTMLImageElement>;

	MARKERSBYID = {
		"towns": "basetilecitadel.png",
	};
	PATH_MARKERS = "assets/graph/fptiles/";
	markerfiles: Map<string, string>;
	markerimages: Map<string, HTMLImageElement>;


	EXTRAIMGS = {
		// "skybgr": "/assets/graph/bgr/sunspearsky.jpg",
		"skybgr": "assets/graph/bgr/sunspearsky_gscale.png"
	};

	extrafiles: Map<string, string>;
	extraimages: Map<string, HTMLImageElement>;

	constructor(public sceneref: SceneRef) {
		super(sceneref);
		this.journeyscene = this.sceneref.scene as Journey;
		console.log("journey path: ", JSON.stringify(this.journeyscene.course));


	}

	ngOnInit() {
		// this.loadAssets();
		// this.prepareAssetsHolders();

	}

	ngAfterViewInit(): void {
		this.viewrenderer = new WalkRenderer(this.journeyscene.game.worldmap, this.renderview);
		let ui = this;
		this.viewrenderer.loadAssets(function ()
		{
			ui.renderStep();
		});
	}


	setCamp()
	{
		console.log("clicked SET CAMP");
	}

	continueJourney()
	{
		console.log("clicked JOURNEY ONWARDs");

		this.journeyscene.step();
		this.journeyscene.game.hero.setCoordinates(this.journeyscene.getCurrentCoordinates());
		if (!this.journeyscene.sceneFinished())
		{
			this.renderStep();
		}
		else {
			this.sceneref.triggerSceneCheck();
		}


	}

	renderStep ()
	{
		const vector = this.journeyscene.getStepDirection(this.journeyscene.stepid);
		this.viewrenderer.renderView(this.journeyscene.getCurrentCoordinates(), vector);
	}

	showMap()
	{
		console.log("clicked SHOW MAP");
	}

	getDirectionDescription (): string {
		let vector = this.journeyscene.getStepDirection(this.journeyscene.stepid);
		let dirstring = "";

		if (vector[1] > 0) {
			dirstring += "South";
		} else if (vector[1] < 0) {
			dirstring += "North";
		}
		if (vector[0] > 0) {
			dirstring += "East";
		} else if (vector[0] < 0) {
			dirstring += "West";
		}

		return dirstring;
	}








}
