import {Component, OnInit, Type} from '@angular/core';
import {SceneUIService} from "../scene-ui/scene-ui.service";
import {HttpClient} from "@angular/common/http";
import {WanderGame} from "../rpg-engine/wander-game";
import {WorldMapViewComponent} from "./scenes/world-map-view/world-map-view.component";
import {SceneRenderer} from "./scenes/scene-renderer";
import {WanderScene} from "../rpg-engine/wander-scene";
import {JourneyDeparture} from "../rpg-engine/scenes/journey-departure";
import {Journey} from "../rpg-engine/scenes/journey";
import {JourneyStepComponent} from "./scenes/journey-step/journey-step.component";
import {Town} from "../rpg-engine/world/locs/Town";
import {TownNav} from "../rpg-engine/scenes/town-nav";
import {TownUIComponent} from "./scenes/town-ui/town-ui.component";
import {Roaming} from "../rpg-engine/scenes/roaming";
import {RoamingStepComponent} from "./scenes/roaming-step/roaming-step.component";

@Component({
	selector: 'app-storyteller',
	templateUrl: './storyteller.component.html',
	styleUrls: ['./storyteller.component.scss']
})
export class StorytellerComponent implements OnInit {

	game: WanderGame;


	constructor(private sceneui: SceneUIService, private http: HttpClient) {
	}

	ngOnInit() {
		this.sceneui.bindStoryTellerUI(this);
		this.loadGameData();

	}

	loadGameData() {
		let stui = this;
		WanderGame.loadGame(this.http, function (game: WanderGame) {
			stui.game = game;
			stui.checkReadyGame();
		});
	}

	checkReadyGame() {
		console.log("running Wander Game ", this.game);

		this.checkUpdateSceneRender();


	}

	getLocationSensitiveScene (): WanderScene
	{
		let ppos = this.game.hero.coordinates;
		let place = this.game.worldmap.getLocationAt(ppos.getCoords());
		console.log("place is town? ", place instanceof Town);
		let scene = null;
		if (place instanceof Town)
		{
			scene = new TownNav (this.game, place);
		}
		else {
			// if all fails, go to map
			scene = new JourneyDeparture(this.game);
		}
		return scene;
	}

	checkUpdateSceneRender()
	{
		console.log("checkUpdateSceneRender");
		let scene = this.game.getActiveScene();
		console.log("checking update on scene ", scene);
		if (!!scene && scene.sceneFinished())
		{
			console.log("having scene finisher");
			scene = scene.getFollowingScene();
			console.log("following scene is ", scene);
		}
		if (!scene)
		{
			scene = this.getLocationSensitiveScene();
		}

		this.game.setActiveScene(scene);
		console.log("using scene ", scene);

		let renderer = this.getSceneRenderer(scene);
		console.log("opening renderer ", renderer, "for scene", scene);
		let ref = this.sceneui.open(renderer, scene);
		console.log("opened as sceneref", ref);
		ref.afterClosed.subscribe(result => {
			console.log('Closed scene', scene, renderer, "w result", result);
		});
	}

	getSceneRenderer (scene: WanderScene): Type<SceneRenderer>
	{
		let renderer;
		if (scene instanceof JourneyDeparture)
		{
			renderer = WorldMapViewComponent;
		}
		else if (scene instanceof Journey)
		{
			renderer = JourneyStepComponent;
		}
		else if (scene instanceof TownNav)
		{
			renderer = TownUIComponent;
		}
		else if (scene instanceof Roaming)
		{
			renderer = RoamingStepComponent;
		}
		return renderer;
	}

}
