import {
	ApplicationRef,
	ComponentFactoryResolver,
	ComponentRef,
	EmbeddedViewRef,
	Injectable,
	Injector, Type
} from '@angular/core';
import {SceneUIComponent} from "./scene-ui.component";
import {SceneRef} from "./scene-ref";
import {SceneInjector} from "./scene-injector";
import {WanderScene} from "../rpg-engine/wander-scene";
import {SceneRenderer} from "../storyteller/scenes/scene-renderer";
import {StorytellerComponent} from "../storyteller/storyteller.component";

@Injectable({
	providedIn: 'root'
})
export class SceneUIService {

	sceneRef: ComponentRef<SceneUIComponent>;
	private storyteller: StorytellerComponent;

	constructor(
		private componentFactoryResolver: ComponentFactoryResolver,
		private appRef: ApplicationRef,
		private injector: Injector) {
	}

	public bindStoryTellerUI (storyteller: StorytellerComponent)
	{
		this.storyteller = storyteller;
	}

	public open(componentType: Type<SceneRenderer>, scene: WanderScene): SceneRef
	{
		let sceneref = this.appendSceneUI(scene);
		this.sceneRef.instance.childComponentType = componentType;
		return sceneref;
	}

	private appendSceneUI (scene: WanderScene): SceneRef
	{

		const map = new WeakMap();
		// map.set(DialogConfig, config);

		// add the DialogRef to dependency injection
		const sceneRef = new SceneRef();
		sceneRef.setContext(scene);
		map.set(SceneRef, sceneRef);

		// we want to know when somebody called the close mehtod
		const sub = sceneRef.afterClosed.subscribe(() => {
			// close the dialog
			console.log("running AFTERCLOSE");
			this.removeSceneUI();
			sub.unsubscribe();

			console.log("getting follower scene ", scene.getFollowingScene());
			this.storyteller.checkUpdateSceneRender();
		});

		const checksub = sceneRef.sceneCheck.subscribe(() => {
			console.log("running SCENECHECK on scene ", scene);
			if (scene.sceneFinished())
			{
				console.log("scene IS finished!");
			}
			else
			{
				console.log("scene NOT finished");
			}
			if (scene.sceneFinished())
			{
				sceneRef.close();
			}
		});

		const componentFactory = this.componentFactoryResolver.resolveComponentFactory(SceneUIComponent);
		const componentRef = componentFactory.create(new SceneInjector(this.injector, map));

		this.appRef.attachView(componentRef.hostView);

		const domElem = (componentRef.hostView as EmbeddedViewRef<any>).rootNodes[0] as HTMLElement;
		document.body.appendChild(domElem);

		this.sceneRef = componentRef;
		// console.log("SceneUI.sceneRef: ", this.sceneRef);
		// console.log("SceneUI.sceneRef.instance: ", this.sceneRef.instance);
		// console.log("SceneUI.sceneRef.childCompo: ", this.sceneRef.instance.componentRef);
		this.sceneRef.instance.onClose.subscribe(() => {
			console.log("running ONCLOSE");
			this.removeSceneUI();
			// console.log("getting follower scene ", scene.getFollowingScene());
			// this.storyteller.checkUpdateSceneRender();
		});

		// return the dialogRef
		return sceneRef;

	}

	private removeSceneUI () {
		console.log("removing scene ref ", this.sceneRef);
		this.appRef.detachView(this.sceneRef.hostView);
		this.sceneRef.destroy();
	}
}
