import {AfterViewInit, Component, OnInit} from '@angular/core';
import {SceneRef} from "../../../scene-ui/scene-ref";
import {SceneRenderer} from "../scene-renderer";
import {Journey} from "../../../rpg-engine/scenes/journey";
import {TownNav} from "../../../rpg-engine/scenes/town-nav";

@Component({
	selector: 'app-town-ui',
	templateUrl: './town-ui.component.html',
	styleUrls: ['./town-ui.component.scss']
})
export class TownUIComponent extends SceneRenderer implements OnInit, AfterViewInit {


	townscene: TownNav;

	constructor(public sceneref: SceneRef) {
		super(sceneref);
		this.townscene = this.sceneref.scene as TownNav;
		console.log("town info: ", JSON.stringify(this.townscene.town));
	}

	ngOnInit() {
	}

	ngAfterViewInit(): void {
	}

	leaveTown ()
	{
		this.townscene.locpoint = TownNav.LOC_LEAVE;
		this.sceneref.triggerSceneCheck();
	}

}
