import {WorldPosition} from "./world/world-position";

export class WanderHero
{

	coordinates: WorldPosition;

	constructor ()
	{
		this.coordinates = null;
	}

	static fromJSON (src: object): WanderHero
	{
		let hero = new WanderHero();

		// TODO: add potentials etc

		// hero.setCoordinates(rawdata["coords"][0], rawdata["coords"][1]);

		hero.setCoordinates(WorldPosition.fromCoordsArray(src["position"]["coords"]));

		return hero;

	}

	setCoordinates (pos: WorldPosition)
	{
		console.log("replacing coords", this.coordinates, "w pos ", pos);
		this.coordinates = pos;
	}


}
